// 请求模块
import Router from '@/router'
import { reLogin } from '@/api/user'
import { Toast } from 'vant'
import axios from 'axios'
import store from '@/store'
const request = axios.create({
  // 项目基准路径
  baseURL: 'http://geek.itheima.net'
})
// 请求拦截器
request.interceptors.request.use(
  (config) => {
    // 请求配置对象
    const { user } = store.state
    if (user && user.token && config.headers.Authorization === undefined) {
      config.headers.Authorization = `Bearer ${user.token}`
    }
    // 必须返回config，不然就卡住了
    return config
  },
  (err) => {
    return Promise.reject(err)
  }
)
// 响应拦截器
request.interceptors.response.use(
  (response) => {
    // 2xx 3xx范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    return response
  },
  async (err) => {
    // 超出 2xx (4xx 4xx)范围的状态码都会触发该函数。
    // 对响应错误做点什么

    // 只有401代表过期，需要跳转登录
    // 不能使用this，这里this指向不知道啥玩意
    if (err.response.status === 401) {
      if (!store.state.user) {
        // 给login页面传当前路由路径
        Router.replace(`/login?path=${Router.currentRoute.fullPath}`)
        Toast({ type: 'warning', message: '登录信息已过期' })
      } else {
        // 方案一强制跳转并清除
        // store.commit('setUser', null)
        // // Notify({ type: 'warning', message: '登录信息已过期' })
        // // setTimeout(() => {
        // Router.replace('/login')

        // 方案二使用refreshtoken替换
        const { data: res } = await reLogin()
        // console.log(res.data.token)
        store.commit('setUser', res.data.token)
        // 把新的token更新在请求头里
        err.config.headers.Authorization = `Bearer ${res.data.token}`
        // 未完成的请求，再一次发起，err.config就是上一次请求配置对象，我们要return回原来的逻辑页面调用地方
        // console.log(err)
        return axios(err.config)
      }
    } else if (err.response.status === 500 && err.config.url === '/v1_0/authorizations' && err.config.method === 'put') {
      store.commit('setUser', null)
      Router.replace(`/login?path=${Router.currentRoute.fullPath}`)
      Toast({ type: 'warning', message: '登录信息已过期' })
    } else {
      Toast('其他错误，稍后再试')
    }
    return Promise.reject(err)
  }
)
export default request
